.itemListStyle{
  margin: 0;
  padding: 0;
  width: 95%;
}
.itemStyle{
  background: linear-gradient(90deg,red,orange);
  color: #002147;
  cursor: pointer;
  list-style: none;
  padding: 10px 20px;
  font-size: 1.3em;
  border-bottom: 0.1px solid #505050;  
  word-wrap:  break-word;
}
.delBtn{
  float: right;
  line-height: 26px;
  color: #002147;
  cursor: pointer;
  transition: 0.2s;
}
.delBtn:hover{
  transform: scale(1.5, 1.5);
  color: red;
}

.done{
  text-decoration: line-through;
}