.inpClass{
  border: none;
  color: #002147;
  text-align: center;
  width: 95%;
  display: inline-block;
  padding: 20px 30px;
  font-size: 1.6em;
  background: linear-gradient(90deg,red,orange);
  opacity: 0.8;
  transition: 0.5;

}
.inpClass::placeholder {
  color: #002147;
}

.delAllBtn{
  color: red;
  cursor: pointer;
  transition: 0.2s;
  font-size: 1.5rem;
  float: right;
}

.delAllBtn:active{
  transform: scale(1.5, 1.5);
}